.App {
  text-align: center;
  display: flex;
}


.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  max-width: 100%;
  padding: 1rem;
}

.header {
  width: 100%;
  background: rgb(2,0,36);
  background: linear-gradient(90deg, rgba(2,0,36,1) 0%, rgba(142,153,179,1) 100%, rgba(0,212,255,1) 100%);
  padding: 2rem;
  margin-bottom: 1rem;
  color: rgba(0,212,255,1);
  font-size: 1.2rem;
}

.content {
  box-shadow: 4px 4px 20px 0px rgba(0,0,0,0.75);
  padding: 1rem;
  text-align: left;
}
.img-text {
  font-size: 1.1rem;
  line-height: 1.5;
}

.POD-img {
  max-width: 100%;
  box-shadow: 4px 4px 50px 0px rgba(0,0,0,0.75);
  margin: 1rem;
}

@media only screen and (min-width: 800px) {
  .content {
    max-width: 50%;
    text-align: center;
  }
 
}